// Reusable Card component for menu items
export function Card({ item, onAdd }: { item: MenuItem; onAdd: (i: MenuItem) => void }) {
  return (
    <article
      className="min-w-[280px] max-w-[340px] bg-white rounded-xl shadow-lg overflow-hidden flex-shrink-0 transition-transform hover:scale-105"
      style={{ scrollSnapAlign: "center" }}
    >
      <div className="relative">
        <img
          src={item.image}
          alt={item.name}
          className="w-full h-52 object-cover transition-transform duration-300 hover:scale-110"
        />
        <button
          aria-label="favorite"
          className="absolute top-4 right-4 bg-white/80 p-2 rounded-full shadow-md text-xl text-gray-500 transition hover:text-red-500"
        >
          ♥
        </button>
        <div className="absolute left-4 bottom-4 bg-white/90 px-4 py-2 rounded-full text-lg font-bold shadow-md">
          ₹{item.price}
        </div>
      </div>
      <div className="p-5">
        <h3 className="text-xl font-bold">{item.name}</h3>
        <p className="text-sm text-gray-600 mt-2">{item.description}</p>
        <button
          onClick={() => onAdd(item)}
          className="mt-4 w-full bg-[#3b2b20] text-white font-semibold py-3 rounded-full transition transform hover:scale-95"
        >
          Add to Cart
        </button>
      </div>
    </article>
  );
}

// Reusable button component for horizontal scrolling
export function ScrollButtons({ scrollRef }: { scrollRef: React.RefObject<HTMLDivElement> }) {
  const scroll = (dir: "left" | "right") => {
    if (!scrollRef.current) return;
    const amount = scrollRef.current.clientWidth * 0.8;
    scrollRef.current.scrollBy({ left: dir === "left" ? -amount : amount, behavior: "smooth" });
  };
  return (
    <div className="absolute top-1/2 left-0 right-0 transform -translate-y-1/2 flex justify-between px-2 md:px-0">
      <button
        aria-label="scroll left"
        onClick={() => scroll("left")}
        className="w-12 h-12 rounded-full bg-white/80 shadow-lg flex items-center justify-center text-2xl text-gray-700 transition transform hover:scale-110"
      >
        ‹
      </button>
      <button
        aria-label="scroll right"
        onClick={() => scroll("right")}
        className="w-12 h-12 rounded-full bg-white/80 shadow-lg flex items-center justify-center text-2xl text-gray-700 transition transform hover:scale-110"
      >
        ›
      </button>
    </div>
  );
}

// Reusable category icon component
export function Category({ icon, label }: { icon: string; label: string }) {
  return (
    <div className="flex flex-col items-center gap-2 transition transform hover:scale-110">
      <div className="w-16 h-16 rounded-full bg-white shadow-md flex items-center justify-center text-3xl">
        {icon}
      </div>
      <p className="text-sm font-medium">{label}</p>
    </div>
  );
}
